import React from 'react';
import Product from './product';
import shirt1 from '../images/shirt-1.jpg';
import shirt2 from '../images/shirt-2.jpg';
import shirt3 from '../images/shirt-3.jpg';
import shirt4 from '../images/shirt-4.jpg';
import './products.css';
const Products = (props) => {
    return (
        <section className="wrapper">
            <h2 className="title">{props.title}</h2>
            <div className="products">
                <Product img={shirt1} />
                <Product img={shirt2} />
                <Product img={shirt3} />
                <Product img={shirt4} />
            </div>
        </section>
    );
}

export default Products;