import React from 'react';
import logo from '../images/material-logo.png';
import address from '../images/address.png';
import email from '../images/phone.png';
import phone from '../images/email.png';
import facebook from '../images/facebook.png';
import instagram from '../images/instagram.png';
import twitter from '../images/twitter.png';
import pinterest from '../images/pinterest.png';
import './footer.css';

const Footer = () => {
    return (
        <footer className="footer">
            <div className="wrapper footer__content">
                <section>
                    <img src={logo} alt="" />
                    <ul className="footer__contact">
                        <li className="footer__contact-item">
                            <img className="contact-item__icon" src={address} alt="" />
                            123 Anystreet Suite 1234 Baltimore, MD 21030
                        </li>
                        <li className="footer__contact-item">
                            <img className="contact-item__icon" src={phone} alt="" />
                            555.555.5535
                        </li>
                        <li className="footer__contact-item">
                            <img className="contact-item__icon" src={email} alt="" />
                            sales@material.com
                        </li>
                    </ul>
                </section>
                <section>
                    <h4 className="footer__header">Product Categories</h4>
                    <ul className="footer__navigation">
                        <li className="footer-navigation__navigation-item"><a className="footer-navigation__link" href="">Tshirts</a></li>
                        <li className="footer-navigation__navigation-item"><a className="footer-navigation__link" href="">Sweatshirts</a></li>
                        <li className="footer-navigation__navigation-item"><a className="footer-navigation__link" href="">Hoodies</a></li>
                        <li className="footer-navigation__navigation-item"><a className="footer-navigation__link" href="">About</a></li>
                        <li className="footer-navigation__navigation-item"><a className="footer-navigation__link" href="">Contact</a></li>
                        <li className="footer-navigation__navigation-item"><a className="footer-navigation__link" href="">About</a></li>
                    </ul>
                </section>
                <section>
                    <h4 className="footer__header">Connect with us</h4>
                    <ul className="footer__social-networks">
                        <li className="footer__social-network"><a href=""><img src={facebook} alt="" /></a></li>
                        <li className="footer__social-network"><a href=""><img src={twitter} alt="" /></a></li>
                        <li className="footer__social-network"><a href=""><img src={pinterest} alt="" /></a></li>
                        <li className="footer__social-network"><a href=""><img src={instagram} alt="" /></a></li>
                    </ul>
                </section>
            </div>
        </footer>
    );
}

export default Footer;