import React from 'react';
import logo from '../images/material-logo.png';
import logo2 from '../images/material-logo2.png';
import './header.css';
const Header = () => {
    return (
        <header>
            
            <div className="wrapper">
                <img className="header__logo" src={logo} alt=""/>
            </div>
            <nav>
                <ul className="wrapper nav">
                    <li className="nav__item"><a className="nav__link" href="#">T-shirts</a></li>
                    <li className="nav__item"><a className="nav__link" href="#">Sweatshirts</a></li>
                    <li className="nav__item"><a className="nav__link" href="#">Hoodies</a></li>
                    <li className="nav__item"><img className="nav__logo" src={logo2} alt=""/></li>
                    <li className="nav__item"><a className="nav__link" href="#">About</a></li>
                    <li className="nav__item"><a className="nav__link" href="#">Contact</a></li>
                    <li className="nav__item"><a className="nav__link" href="#">Account</a></li>
                </ul>
            </nav>
        </header>
    )
}

export default Header;