import React from 'react';
import star from '../images/star.png';
import Button from './button';
import './product.css';
const Product = (props) => {
    return (
        <div className="product">
            <img src={props.img} alt=""/>
            <p className="product__title">Awesome Hoodie Product in Night Blue </p>
            <ul className="product__stars">
                <li className="product__star"><img className="product__star-icon" src={star} alt=""/></li>
                <li className="product__star"><img className="product__star-icon" src={star} alt=""/></li>
                <li className="product__star"><img className="product__star-icon" src={star} alt=""/></li>
                <li className="product__star"><img className="product__star-icon" src={star} alt=""/></li>
                <li className="product__star"><img className="product__star-icon" src={star} alt=""/></li>
            </ul>
            <p><span className="product__old-price">$40.00</span> $26.00</p>
            <Button size="small">add to cart</Button>
        </div>
    )
}

export default Product;