import React from 'react';
import './button.css';

const Button = (props) => {
    let size = "";
    const classes = ["button"];
    console.log(props.size);
    switch(props.size) {
        case "small":
            classes.push("button--small");
            break;
        case "medium":
            classes.push("button--medium");
            break;
        default:
            break;
    }
    return (
        <button className={classes.join(' ')}>{props.children}</button>
    )
}

export default Button;